/**
 * 1.  padding  margin    -    .
 * 2.       ,  .. -.
 * 3.   -    .
 * 4.     .
 * 5.  FF  MacOS   blur      mousedown.
 *    : http://jsfiddle.net/mishaberezin/892yppts/10/.
 * 6.  -.
 * 7.     iOS  Android.
 * 8.  IE   hidden,    visible,  .
 *      visible, ..      .
 * 9.       .
 * 10.     ,    link.
 * 11.  IOS 15  button[type=submit]  bold
 */
.Button2 {
    position: relative;

    display: inline-block; /* 6 */
    overflow: visible; /* 8 */

    box-sizing: border-box;
    margin: 0; /* 1 */
    padding: 0; /* 1 */

    font-family: var(--button-font-family);
    font-weight: inherit; /* 11 */
    -moz-user-focus: ignore; /* 5 */

    user-select: none; /* 2 */
    text-align: center; /* 10 */
    white-space: nowrap; /* 4 */
    text-decoration: none; /* 3 */

    border-width: 0;
    outline: 0;
    background: transparent; /* 9 */
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0); /* 7 */

    touch-action: manipulation;
}

/**
 *     ,      ,
 *     ,      disabled.
 */
.Button2[aria-disabled='true'],
.Button2[aria-busy='true'] {
    pointer-events: none;
}

/**
 *  FF 4+      ,   
 *       css- border  background.
 *    ,        .
 * : http://stackoverflow.com/questions/8859908.
 */
.Button2::-moz-focus-inner {
    padding: 0;

    border: 0;
}

.Button2::before {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    content: '';

    border-radius: var(--button-borderRadius);
}

/**
 *       
 * https://st.yandex-team.ru/ISL-5480
 */
.Button2::after {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    content: '';

    transform: scale(0);
}
